<template>
  <Modal
    :active="showModal"
    :event-name="'company-map-modal'"
    :full-screen="'nexus'"
    :custom-width="'w-full ipadPro:w-[1024px]'"
    :custom-padding="selectLocation ? 'p-spacing-0' : ''"
  >
    <div class="relative h-full w-full">
      <div
        class="absolute z-[100000] flex h-spacing-400 w-spacing-400 cursor-pointer items-center justify-center rounded-full bg-bg-neutral-default"
        :class="
          selectLocation
            ? 'right-spacing-250 top-spacing-250'
            : 'right-spacing-100 top-spacing-100'
        "
        @click="handleClose()"
      >
        <IconX
          class="h-spacing-300 w-spacing-300 text-content-on-neutral-secondary hover:text-content-on-neutral-primary"
        />
      </div>

      <SelectLocation
        v-if="selectLocation"
        :selected-location-coords="selectedLocationCoords"
        :map-container-id="'select-location-on-map'"
        class="ipad:h-[90dvh]"
      />
      <MapComponent
        v-else
        :markers-data="markersData"
        :map-container-id="'company-map-in-modal'"
      />
    </div>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    MapComponent: () => import("@/components/MapComponent/index.vue"),
    SelectLocation: () =>
      import("@/components/MapComponent/SelectLocation.vue"),
    IconX: () => import("@/components/svgIcons/x.vue"),
  },
  data() {
    return {
      showModal: false,
      markersData: [],
      selectedLocationCoords: null,
      selectLocation: null,
    };
  },
  mounted() {
    this.$bus.$on("company-map-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.markersData = data?.markersData;
        this.selectLocation = data?.selectLocation;
        this.selectedLocationCoords = data?.selectedLocationCoords;
      } else {
        this.showModal = false;
        this.selectLocation = false;
        this.selectedLocationCoords = null;
        this.markersData = [];
      }
    });
  },
  methods: {
    handleClose() {
      this.$bus.$emit("company-map-modal", { showModal: false });
    },
  },
  beforeDestroy() {
    this.$bus.$off("company-map-modal");
  },
};
</script>
